// Modal.tsx
import React from 'react';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Button,
} from "@mui/material";
import { IClient, IServer } from '../../../models/types';
import "./_modal.scss"

interface ModalProps {
    selectedRow: IClient | IServer | null;
    handleCloseModal: () => void;
}

const ModalClient: React.FC<ModalProps> = ({ selectedRow, handleCloseModal }) => {
    if (!selectedRow) {
        return null;
    }

    return (
        <Dialog open={true} onClose={handleCloseModal} maxWidth="xl">
            <>
                <DialogTitle> {selectedRow.name}</DialogTitle>
                <DialogContent style={{ minWidth: '1000px' }}>
                    <div style={{display: "flex", justifyContent:"space-between"}}>
                        {/* */}
                        <div style={{width: "485px"}}>
                            <h3>  :</h3>
                            <div style={{border: "1px double black", padding: "10px"}}>
                                : <b>{selectedRow.modal.deviceInformation.id}</b><br/>
                                 : <b>{selectedRow.modal.deviceInformation.timeWork}</b><br/>
                                  : <b>{selectedRow.modal.deviceInformation.timeWorkPo}</b><br/>
                                GPS/: <b>{selectedRow.modal.deviceInformation.gpsGlonass}</b><br/>
                                 : <b style={{color:"green"}}>{selectedRow.modal.deviceInformation.systemTime}</b><br/>
                                 : <b style={{color:"green"}}>{selectedRow.modal.deviceInformation.configuredCameras}</b><br/>
                                -  : <b style={{color:"green"}}>{selectedRow.modal.deviceInformation.noConnected}</b><br/>
                                -  : <b style={{color:"green"}}>{selectedRow.modal.deviceInformation.withRemarks}</b><br/>
                                - : <b style={{color:"green"}}>{selectedRow.modal.deviceInformation.connected}</b><br/>
                            </div>

                            <div>
                                <h3>:</h3>
                                <p style={{border: "1px double black", padding: "10px"}}>: <b style={{color:"red"}}>{selectedRow.modal.unloading}</b></p>
                            </div>

                            <div>
                                <h3> :</h3>
                                <div style={{border: "1px double black", padding: "10px"}}>
                                     : <b>{selectedRow.modal.software.recognitionLibrary}</b> <br/>
                                     : <b>{selectedRow.modal.software.timeLibrary}</b><br/>
                                      : <b>{selectedRow.modal.software.unloadModule}</b><br/>
                                     : <b>{selectedRow.modal.software.webInterface}</b><br/>
                                      : <b>{selectedRow.modal.software.videoCaptureModule}</b>
                                </div>
                            </div>

                        </div>
                        {/* */}
                        <div style={{width: "485px"}}>
                            <h3> </h3>
                            <div style={{border: "1px double black", padding: "10px"}}>
                                 : <b>{selectedRow.modal.cpuModule.ramSize.total}Mb  <span style={{color: "green"}}>{selectedRow.modal.cpuModule.ramSize.free}Mb</span></b> <br/>
                                <h3>  CPU</h3>
                                <div style={{border: "1px double black", padding: "10px"}}>
                                    : <b>{selectedRow.modal.cpuModule.informationCpu.nameCpu}</b> <br/>
                                     : <b><span style={{color: "green"}}>{selectedRow.modal.cpuModule.informationCpu.averageLoad1}%</span>,
                                    <span style={{color: "green"}}> {selectedRow.modal.cpuModule.informationCpu.averageLoad2}%</span></b> <br/>
                                    .  : <b><span style={{color: "green"}}>{selectedRow.modal.cpuModule.informationCpu.maxKernelLoading1}%</span>,
                                    <span style={{color: "green"}}> {selectedRow.modal.cpuModule.informationCpu.maxKernelLoading2}%</span></b> <br/>
                                    .  : <b><span style={{color: "green"}}>{selectedRow.modal.cpuModule.informationCpu.maxStreamLoading1}%</span>,
                                    <span style={{color: "green"}}> {selectedRow.modal.cpuModule.informationCpu.maxStreamLoading2}%</span></b> <br/>
                                     : <b><span style={{color: "green"}}>{selectedRow.modal.cpuModule.informationCpu.batchTemperature1}%</span>,
                                    <span style={{color: "green"}}> {selectedRow.modal.cpuModule.informationCpu.batchTemperature2}%</span></b> <br/>
                                    : <b>Package: {selectedRow.modal.cpuModule.informationCpu.consumption1},
                                    Package: {selectedRow.modal.cpuModule.informationCpu.consumption2}</b>

                                </div>
                                <h3>HDD:</h3>
                                <div style={{border: "1px double black", padding: "5px 5px", justifyContent: "space-between"}}>
                                    <h4 style={{textAlign:"center", margin: '5px'}}> :</h4>
                                    <table className={'table_modal'}>
                                        <tbody>
                                        <tr>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                            <th>.</th>
                                        </tr>

                                        <tr>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.physicalDisk.model}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.physicalDisk.series}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.physicalDisk.size}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.physicalDisk.temp}</td>
                                        </tr>
                                        </tbody>
                                    </table>

                                    <h4 style={{textAlign:"center", margin: '5px'}}> :</h4>
                                    <table className={'table_modal'}>
                                        <tbody>
                                        <tr>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                        </tr>

                                        <tr>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk1.disk}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk1.volume}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk1.free}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk1.threshold}</td>
                                        </tr>

                                        <tr>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk2.disk}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk2.volume}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk2.free}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk2.threshold}</td>
                                        </tr>

                                        <tr>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk3.disk}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk3.volume}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk3.free}</td>
                                            <td >{selectedRow.modal.cpuModule.informationHdd.logicalDisk3.threshold}</td>
                                        </tr>
                                        </tbody>

                                    </table>

                                </div>

                            </div>



                        </div>

                    </div>

                    <div>
                        <h3 style={{marginTop: '-10px'}} > :</h3>
                        <table className={'table_modal'}>
                            <tbody>
                            <tr>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th>ip - mask</th>
                                <th>.</th>
                            </tr>

                            <tr>
                                <td >{selectedRow.modal.networkInterfaces.name}</td>
                                <td >{selectedRow.modal.networkInterfaces.speed} Mb/s</td>
                                <td >{selectedRow.modal.networkInterfaces.mac}</td>
                                <td >{selectedRow.modal.networkInterfaces.ipMask}</td>
                                <td >IN - {selectedRow.modal.networkInterfaces.currentSpeedIn} Kb/s
                                    <br/>OUT - {selectedRow.modal.networkInterfaces.currentSpeedOut} Kb/s </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>

                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseModal} color="primary">
                        Close
                    </Button>
                </DialogActions>
            </>
        </Dialog>
    );
};

export default ModalClient;
